                      after_medical_obligatory_exam:
                        after_medical_obligatory_exam_arr + "",
                      after_treatment_drug_usage_drug_usage_antibio_usage_num: this
                        .after_treatment_drug_usage_drug_usage_antibio_usage_num,
                      after_treatment_drug_usage_drug_usage_anaesthetic_usage_num: this
                        .after_treatment_drug_usage_drug_usage_anaesthetic_usage_num,
                      after_treatment_drug_usage_drug_usage_otherdrugs_usage_num: this
                        .after_treatment_drug_usage_drug_usage_otherdrugs_usage_num,
                      after_treatment_drug_usage_antibio_usage_id:
                        after_treatment_drug_usage_antibio_usage_id_arr + "",
                      after_treatment_drug_usage_anaesthetic_usage_num:
                        after_treatment_drug_usage_anaesthetic_usage_num_arr +
                        "",
                      after_treatment_drug_usage_otherdrugs_usage_num:
                        after_treatment_drug_usage_otherdrugs_usage_num_arr +
                        "",
                      discharge_criteria: this.discharge_criteria,
                      discharge_criteria_item: discharge_criteria_item_arr + "",
                      other_notice: this.other_notice,
                      other_notice_item: other_notice_item_arr + "",
                      submitter: this.submitter
                    }
                  })
                  .then(action => {
                    this.$message({
                      message: "恭喜你，提交成功",
                      type: "success"
                    });
                    setTimeout(window.location.reload(), 2000);
                    // 提交完成后刷新页面
                  })
                  .catch(error => {
                    this.$message.error("哦豁~~数据提交失败啦~~");
                  });

                setTimeout(() => {
                  instance.confirmButtonLoading = false;
                }, 300);
              }, 1200);
            } else {
              done();
            }
          }
        }).catch(() => {
          this.$message("好啦~~数据没有提交呦~~");
        });
      }
    }
  }
};
</script>

