<template>
  <div class="kangjun">
    <span class="shu">
      {{index+1}}
      <input type="text" class="wen" v-model="content_item_id" name="content_item" />
    </span>
  </div>
</template>


<script>
export default {
  data() {
    return {
      content_item_id: ""
    };
  },
  props: ["index"]
};
</script>


<style scoped>
.shu {
  display: inline-block;
  margin-left: -40px;
  color: darkmagenta;
}
.wen {
  width: 280px;
  height: 30px;
  margin-top: 10px;
  margin-left: 3px;
}
.kangjun {
  margin-left: 238px;
}
</style>



