<template>
  <div>
    <span
      class="txt"
      style="float: left; color: blue; font-size: 15px"
    >标准{{index+1}}路径:&nbsp; &nbsp; &nbsp;</span>
    <textarea
      v-model="entry_standard_id"
      class="Applicableobject"
      style="resize:none;"
      name="entry_standard_id_w"
    ></textarea>
  </div>
</template>
<script>
export default {
  data() {
    return {
      entry_standard_id: ""
    };
  },
  props: ["index"]
};
</script>
 <style scoped>
.txt {
  display: inline-block;
  margin: 42px 40px;
}
.Applicableobject {
  display: inline-block;
  float: right;
  margin-top: 20px;
  width: 300px;
  height: 80px;
}
</style>






