<template>
  <div class="zhunbei">
    <span class="shu">准备工作{{index+1}}</span>
    <br />
    <div class="contentxia">
      <span class="content">准备工作内容：</span>
      <textarea
        v-model="prep_treatment_extension_content"
        class="Applicableobject"
        style="resize:none;"
        name="prep_treatment_extension_content"
      ></textarea>
      <br />
      <span class="shuzi">
        子条目总数 ：
        <input
          type="text"
          v-model="content_item"
          class="shuinput"
          name="content_item_num"
          :onchange="watchNumber(content_item)"
        />
        <el-button @click="add">添加</el-button>
      </span>
      <formiput
        v-for="(item,index) in content_item_num"
        :key="'add'+index"
        :index="index"
        :content_item_num=" content_item_num"
      ></formiput>
    </div>
  </div>
</template>

<script>
import formiput from "./addZhiliaosun";

export default {
  data() {
    return {
      prep_treatment_extension_content: "",
      content_item: "",
      content_item_num: ""
    };
  },
  props: ["index"],
  components: {
    formiput
  },
  methods: {
    watchNumber(number) {
      let Wnumber = number;
      if (Wnumber < 0) {
        this.$message({
          showClose: true,
          message: "若输入值为负,点击添加会死机呦",
          type: "error"
        });
      }
    },
    add() {
      let value = this.content_item;
      if (value == "") {
        this.$message.error("输入框为空不能添加");
        return false;
      }
      if (!isNaN(value)) {
        this.content_item_num = parseInt(this.content_item);
        return false;
      } else if (isNaN(value)) {
        this.$message({
          message: "请输入数字",
          type: "warning"
        });
        return false;
      }
    }
  }
};
</script>

<style scoped>
.shu {
  margin: 0 -14px;
  color: darkmagenta;
}
.zhunbei {
  margin-left: 100px;
  margin-top: 20px;
}
.Applicableobject {
  display: inline-block;
  float: right;
  margin-top: -4px;
  width: 300px;
  height: 50px;
}
.content {
  margin-top: 20px;
  display: inline-block;
}
.contentxia {
  margin-top: 10px;
}
.shuzi {
  color: green;
  margin-top: 50px;
  display: inline-block;
}
.shuinput {
  display: inline-block;
  text-align: center;
  margin-left: 240px;
  width: 60px;
  height: 30px;
}
</style>



