<template>
  <div>
    <div class="treatment_days">
      <span class="txt">场景{{index+1}}:&nbsp; &nbsp; &nbsp;</span>
      <el-select v-model="prep_treatment_days" placeholder="请选择手术时间段单位" name="prep_treatment_days">
        <el-option
          v-for="item in duration_time_unit"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <input
        type="text"
        class="shijian"
        placeholder="最小值"
        v-model="duration_min"
        name="prep_duration_min"
        :onchange="WatchNumber(duration_min)"
      />
      <input
        type="text"
        class="shijian"
        placeholder="最大值"
        v-model="duration_max"
        name="prep_duration_max"
        @blur="Contrast(duration_max)"
        :onchange="WatchNumber(duration_max)"
      />
      <br />
      <!---------------------------------------------------- -->
      <span class="text">输入必须检查的项目条数:&nbsp; &nbsp; &nbsp;</span>
      <el-input
        v-model="obligatory_exam"
        placeholder="条目总数"
        name="obligatory_exam"
        :onchange="watchNumber(obligatory_exam)"
      ></el-input>
      <el-button @click="add_obligatory_exam" type="primary">一键添加</el-button>
      <Frombijian
        v-for="(item, index) in obligatory_exam_num"
        :key="'obligatory_exam_num'+index"
        :index="index"
        :obligatory_exam_num="obligatory_exam_num"
      ></Frombijian>
      <br />
      <!-- ----------------------------------------------------------------- -->
      <!-- @change="keJian" -->
      <span class="text">输入可选检查的项目条数:&nbsp; &nbsp; &nbsp;</span>
      <el-input
        v-model="optional_exam"
        placeholder="条目总数"
        name="optional_exam"
        :onchange="watchNumber(optional_exam)"
      ></el-input>
      <el-button @click="add_optional_exam" type="primary">一键添加</el-button>
      <Fromkejian
        v-for="(item, index) in optional_exam_num"
        :key="'optional_exam_num'+index"
        :index="index"
        :optional_exam_num="optional_exam_num"
      ></Fromkejian>
      <br />
      <!-- ------------------------------------------------------------------ -->
      <span class="text">输入必要的告知信息条数:&nbsp; &nbsp; &nbsp;</span>
      <el-input
        v-model="notification"
        placeholder="条目总数"
        name="notification"
        :onchange="watchNumber(notification)"
      ></el-input>
      <el-button @click="add_notification" type="primary">一键添加</el-button>
      <Frombigao
        v-for="(item, index) in notification_num"
        :key="'notification_num'+index"
        :index="index"
        :notification_num="notification_num"
      ></Frombigao>
    </div>
  </div>
</template>
<script>
import Frombijian from "./Bijian";
import Fromkejian from "./Kejian";
import Frombigao from "./Bigao";
export default {
  data() {
    return {
      prep_treatment_days: "",
      duration_time_unit: [
        {
          value: "日",
          label: "日"
        },
        {
          value: "时",
          label: "时"
        },
        {
          value: "周",
          label: "周"
        },
        {
          value: "月",
          label: "月"
        }
      ],
      duration_min: "",
      duration_max: "",
      obligatory_exam: "",
      optional_exam: "",
      notification: "",
      obligatory_exam_num: "",
      optional_exam_num: "",
      notification_num: ""
    };
  },
  props: ["index"],
  components: {
    Frombijian,
    Fromkejian,
    Frombigao
  },

  methods: {
    Contrast(number) {
      let Min = this.duration_min;
      if (number < Min) {
        this.$notify.error({
          title: "错误",
          message: "最大值不能小于最小值"
        });
      }
    },
    WatchNumber(number) {
      let Wnumber = number;
      if (Wnumber < 0) {
        this.$message({
          showClose: true,
          message: "不能输入负值呦",
          type: "error"
        });
      }
    },
    watchNumber(number) {
      let Wnumber = number;
      if (Wnumber < 0) {
        this.$message({
          showClose: true,
          message: "若输入值为负,点击添加会死机呦",
          type: "error"
        });
      }
    },
    add_obligatory_exam() {
      let value = this.obligatory_exam;
      if (value == "") {
        this.$message.error("输入框为空不能添加");
        return false;
      }
      if (!isNaN(value)) {
        this.obligatory_exam_num = parseInt(this.obligatory_exam);
        return false;
      } else if (isNaN(value)) {
        this.$message({
          message: "请输入数字",
          type: "warning"
        });
        return false;
      }
    },
    add_optional_exam() {
      let value = this.optional_exam;
      if (value == "") {
        this.$message.error("输入框为空不能添加");
        return false;
      }
      if (!isNaN(value)) {
        this.optional_exam_num = parseInt(this.optional_exam);
        return false;
      } else if (isNaN(value)) {
        this.$message({
          message: "请输入数字",
          type: "warning"
        });
        return false;
      }
    },
    add_notification() {
      let value = this.notification;
      if (value == "") {
        this.$message.error("输入框为空不能添加");
        return false;
      }
      if (!isNaN(value)) {
        this.notification_num = parseInt(this.notification);
        return false;
      } else if (isNaN(value)) {
        this.$message({
          message: "请输入数字",
          type: "warning"
        });
        return false;
      }
    }
  }
};
</script>
<style scoped>
.txt {
  color: green;
}
.text {
  color: brown;
}
.treatment_days {
  margin-top: 25px;
  margin-left: 66px;
}
.shijian {
  width: 80px;
  height: 30px;
  text-align: center;
  margin-left: 15px;
}
</style>




