<template>
  <div>
    <span
      class="txt"
      style="float: left; color: green; font-size: 15px"
    >医疗方案子条目{{index+1}}:&nbsp; &nbsp; &nbsp;</span>
    <span class="txt">
      医疗内容：
      <textarea
        v-model="treatment_plan_num_id_content"
        class="Applicableobject"
        style="resize:none;"
        name="treatment_plan_num_id_content"
      ></textarea>
    </span>
    <span class="txt" style="color:orange">
      医疗内容子条目：
      <input
        type="text"
        v-model="treatment_plan_num_id_content_item"
        class="time-num"
        name="treatment_plan_num_id_content_item_num"
        :onchange="watchNumber(treatment_plan_num_id_content_item)"
      />
      <el-button @click="addYiliao">添加</el-button>
    </span>
    <Addyiliao
      v-for="(item, index) in treatment_plan_num_id_content_item_id"
      :key="'addyiliao'+index"
      :index="index"
      :treatment_plan_num_id_content_item_id="treatment_plan_num_id_content_item_id"
    ></Addyiliao>
  </div>
</template>
<script>
import Addyiliao from "./addSschangjYiliao";
export default {
  data() {
    return {
      treatment_plan_num_id_content: "",
      treatment_plan_num_id_content_item: "",
      treatment_plan_num_id_content_item_id: ""
    };
  },
  props: ["index"],
  components: { Addyiliao },
  methods: {
    watchNumber(number) {
      let Wnumber = number;
      if (Wnumber < 0) {
        this.$message({
          showClose: true,
          message: "若输入值为负,点击添加会死机呦",
          type: "error"
        });
      }
    },
    addYiliao() {
      let value = this.treatment_plan_num_id_content_item;
      if (value == "") {
        this.$message.error("输入框为空不能添加");
        return false;
      }
      if (!isNaN(value)) {
        this.treatment_plan_num_id_content_item_id = parseInt(
          this.treatment_plan_num_id_content_item
        );
        return false;
      } else if (isNaN(value)) {
        this.$message({
          message: "请输入数字",
          type: "warning"
        });
        return false;
      }
    }
  }
};
</script>
 <style scoped>
.txt {
  display: inline-block;
  margin: 10px 60px;
}
.Applicableobject {
  display: inline-block;
  float: right;
  margin-top: -18px;
  width: 300px;
  height: 80px;
  margin-left: 235px;
}
.time-num {
  text-align: center;
  margin-top: 21px;
  margin-left: 168px;
  width: 100px;
  height: 30px;
}
</style>






