<!--  -->
<template>
  <div>
    <div style="width:100%;">
      <div>
        <span style="color:blue">输入场景{{index+1}}的条目数目：</span>
        <el-input
          v-model="scenario_num_id"
          name="treatment_choice_scenario_num"
          :onchange="watchNumber(scenario_num_id)"
        ></el-input>
        <el-button @click="addcurrentIndex" type="primary">一键添加</el-button>
        <formsun
          v-for="(item, inde) in num_length"
          :key="inde"
          :inde="inde"
          :index="index"
          :num_length="num_length"
        ></formsun>
      </div>
    </div>
  </div>
</template>

<script>
import formsun from "./form-sun";
export default {
  data() {
    return {
      scenario_num_id: "",
      num_length: "",
      input: "",
      items: [
        {
          num: 1,
          name: ""
        }
      ]
      // input1: "",
      // num2: 2,
      // currentIndex: this.index
    };
  },

  props: ["index"],

  components: {
    formsun
  },

  computed: {},

  created() {},

  mounted() {},

  methods: {
    watchNumber(number) {
      let Wnumber = number;
      if (Wnumber < 0) {
        this.$message({
          showClose: true,
          message: "若输入值为负,点击添加会死机呦",
          type: "error"
        });
      }
    },
    addcurrentIndex() {
      let value = this.scenario_num_id;
      if (value == "") {
        this.$message.error("输入框为空不能添加");
        return false;
      }
      if (!isNaN(value)) {
        this.num_length = parseInt(this.scenario_num_id);
        return false;
      } else if (isNaN(value)) {
        this.$message({
          message: "请输入数字",
          type: "warning"
        });
        return false;
      }
    }
  }
};
</script>

<style scoped>
</style>




