<template>
  <div>
    <span
      class="txt"
      style="float: left; color: blue; font-size: 15px"
    >手术场景{{index+1}}:&nbsp; &nbsp; &nbsp;</span>
    <span class="txt">
      医疗方案的选择依据：
      <textarea
        v-model="treatment_plan_ref"
        class="Applicableobject"
        style="resize:none;"
        name="treatment_plan_ref"
      ></textarea>
    </span>

    <span class="txt">
      输入医疗方案的子条目数：
      <input
        type="text"
        v-model="treatment_plan_num"
        class="time-num"
        name="treatment_plan_num"
        :onchange="watchNumber(treatment_plan_num)"
      />
      <el-button @click="addShoushuZ">添加</el-button>
    </span>
    <FromYiliaoZ
      v-for="(item, index) in treatment_plan_num_id"
      :key="'addsschangj'+index"
      :index="index"
      :treatment_plan_num_id="treatment_plan_num_id"
    ></FromYiliaoZ>
    <!-- ------------------------------------------- -->
    <div class="treatment_days">
      <span class="bijian">输入治疗方案必须检查项目数:&nbsp; &nbsp; &nbsp;</span>
      <el-input
        v-model="treatment_scenario_obligatory_exam"
        placeholder="条目总数"
        name="treatment_scenario_obligatory_exam_num"
        :onchange="watchNumber(treatment_scenario_obligatory_exam)"
      ></el-input>
      <el-button @click="add_zizhi" type="primary">一键添加</el-button>
      <FromZizhi
        v-for="(item, index) in treatment_scenario_obligatory_exam_num"
        :key="'treatment_scenario_obligatory_exam'+index"
        :index="index"
        :treatment_scenario_obligatory_exam_num="treatment_scenario_obligatory_exam_num"
      ></FromZizhi>
      <br />
      <!-- ----------------------------------------------------------------- -->
      <span class="bijian">输入治疗方案可选的检查项目数:&nbsp; &nbsp; &nbsp;</span>
      <el-input
        v-model="treatment_scenario_optional_exam"
        placeholder="条目总数"
        name="treatment_scenario_optional_exam_num"
        :onchange="watchNumber(treatment_scenario_optional_exam)"
      ></el-input>
      <el-button @click="add_Kexuan" type="primary">一键添加</el-button>
      <FromKejian
        v-for="(item, index) in treatment_scenario_optional_exam_num"
        :key="'treatment_scenario_optional_exam_num'+index"
        :index="index"
        :treatment_scenario_optional_exam_num="treatment_scenario_optional_exam_num"
      ></FromKejian>
      <br />
      <!-- ----------------------------------------------------------------- -->
      <span class="bijian">输入治疗方案必要的告知信息数:&nbsp; &nbsp; &nbsp;</span>
      <el-input
        v-model="treatment_scenario_notification"
        placeholder="条目总数"
        name="treatment_scenario_notification_num"
        :onchange="watchNumber(treatment_scenario_notification)"
      ></el-input>
      <el-button @click="add_Bigao" type="primary">一键添加</el-button>
      <FromBigao
        v-for="(item, index) in treatment_scenario_notification_num"
        :key="'treatment_scenario_notification'+index"
        :index="index"
        :treatment_scenario_notification_num="treatment_scenario_notification_num"
      ></FromBigao>
      <br />
    </div>
  </div>
</template>
<script>
import FromYiliaoZ from "./addSschangjSun";
import FromZizhi from "./addSschangZizhi";
import FromKejian from "./addSschangjZike";
import FromBigao from "./addSschangBigao";
export default {
  data() {
    return {
      treatment_plan_ref: "",
      treatment_plan_num: "",
      treatment_plan_num_id: "",
      treatment_scenario_obligatory_exam: "",
      treatment_scenario_obligatory_exam_num: "",
      treatment_scenario_optional_exam: "",
      treatment_scenario_optional_exam_num: "",
      treatment_scenario_notification: "",
      treatment_scenario_notification_num: ""
    };
  },
  props: ["index"],
  components: {
    FromYiliaoZ,
    FromZizhi,
    FromKejian,
    FromBigao
  },
  methods: {
    watchNumber(number) {
      let Wnumber = number;
      if (Wnumber < 0) {
        this.$message({
          showClose: true,
          message: "若输入值为负,点击添加会死机呦",
          type: "error"
        });
      }
    },
    addShoushuZ() {
      let value = this.treatment_plan_num;
      if (value == "") {
        this.$message.error("输入框为空不能添加");
        return false;
      }
      if (!isNaN(value)) {
        this.treatment_plan_num_id = parseInt(this.treatment_plan_num);
        return false;
      } else if (isNaN(value)) {
        this.$message({
          message: "请输入数字",
          type: "warning"
        });
        return false;
      }
    },
    add_zizhi() {
      let value = this.treatment_scenario_obligatory_exam;
      if (value == "") {
        this.$message.error("输入框为空不能添加");
        return false;
      }
      if (!isNaN(value)) {
        this.treatment_scenario_obligatory_exam_num = parseInt(
          this.treatment_scenario_obligatory_exam
        );
        return false;
      } else if (isNaN(value)) {
        this.$message({
          message: "请输入数字",
          type: "warning"
        });
        return false;
      }
    },
    add_Kexuan() {
      let value = this.treatment_scenario_optional_exam;
      if (value == "") {
        this.$message.error("输入框为空不能添加");
        return false;
      }
      if (!isNaN(value)) {
        this.treatment_scenario_optional_exam_num = parseInt(
          this.treatment_scenario_optional_exam
        );
        return false;
      } else if (isNaN(value)) {
        this.$message({
          message: "请输入数字",
          type: "warning"
        });
        return false;
      }
    },
    add_Bigao() {
      let value = this.treatment_scenario_notification;
      if (value == "") {
        this.$message.error("输入框为空不能添加");
        return false;
      }
      if (!isNaN(value)) {
        this.treatment_scenario_notification_num = parseInt(
          this.treatment_scenario_notification
        );
        return false;
      } else if (isNaN(value)) {
        this.$message({
          message: "请输入数字",
          type: "warning"
        });
        return false;
      }
    }
  }
};
</script>
 <style scoped>
.txt {
  display: inline-block;
  margin: 10px 60px;
}
.Applicableobject {
  display: inline-block;
  float: right;
  margin-top: -18px;
  width: 300px;
  height: 80px;
  margin-left: 235px;
}
.time-num {
  text-align: center;
  margin-top: 21px;
  margin-left: 100px;
  width: 100px;
  height: 30px;
}
.treatment_days {
  margin-top: 25px;
  margin-left: 66px;
}
.bijian {
  color: coral;
}
</style>






