<template>
  <div>
    <div class="treatment_days">
      <span class="txt">术后场景{{index+1}}:&nbsp; &nbsp; &nbsp;</span>
      <br />
      <span class="text">输入术后恢复需要的治疗项目数:&nbsp; &nbsp; &nbsp;</span>
      <el-input
        v-model="after_medical_treatment_scenario_recovery_plan"
        placeholder="条目总数"
        name="after_medical_treatment_recovery_plan"
        :onchange="watchNumber(after_medical_treatment_scenario_recovery_plan)"
      ></el-input>
      <el-button @click="add_afterxu" type="primary">一键添加</el-button>
      <FromHouxu
        v-for="(item, index) in after_medical_treatment_scenario_recovery_plan_num"
        :key="'after_obligatory_exam_num'+index"
        :index="index"
        :after_medical_treatment_scenario_recovery_plan_num="after_medical_treatment_scenario_recovery_plan_num"
      ></FromHouxu>
      <br />
      <!-- ----------------------------------------------------------------- -->
      <span class="text">输入术后恢复必须的检查项目数:&nbsp; &nbsp; &nbsp;</span>
      <el-input
        v-model="after_medical_treatment_scenario_obligatory_exam"
        placeholder="条目总数"
        name="after_medical_obligatory_exam"
        :onchange="watchNumber(after_medical_treatment_scenario_obligatory_exam)"
      ></el-input>
      <el-button @click="add_afterbi" type="primary">一键添加</el-button>
      <FromHoubi
        v-for="(item, index) in after_medical_treatment_scenario_obligatory_exam_num"
        :key="'after_optional_exam_num'+index"
        :index="index"
        :after_medical_treatment_scenario_obligatory_exam_num="after_medical_treatment_scenario_obligatory_exam_num"
      ></FromHoubi>
      <br />
      <!-- ------------------------------------------------------------------ -->
      <span class="text">输入术后恢复可选的检查项目数&nbsp; &nbsp; &nbsp;</span>
      <el-input
        v-model="after_medical_treatment_scenario_optional_exam"
        placeholder="条目总数"
        name="after_medical_optional_exam"
        :onchange="watchNumber(after_medical_treatment_scenario_optional_exam)"
      ></el-input>
      <el-button @click="add_afterke" type="primary">一键添加</el-button>
      <FromHouke
        v-for="(item, index) in after_medical_treatment_scenario_optional_exam_num"
        :key="'after_notification_num'+index"
        :index="index"
        :after_medical_treatment_scenario_optional_exam_num="after_medical_treatment_scenario_optional_exam_num"
      ></FromHouke>
    </div>
  </div>
</template>
<script>
import FromHouxu from "./addWcChangjXj";
import FromHoubi from "./addWcChangjBj";
import FromHouke from "./addWcChangjKj";
export default {
  data() {
    return {
      after_medical_treatment_scenario_recovery_plan: "",
      after_medical_treatment_scenario_recovery_plan_num: "",
      after_medical_treatment_scenario_obligatory_exam: "",
      after_medical_treatment_scenario_obligatory_exam_num: "",
      after_medical_treatment_scenario_optional_exam: "",
      after_medical_treatment_scenario_optional_exam_num: ""
    };
  },
  props: ["index"],
  components: {
    FromHouxu,
    FromHoubi,
    FromHouke
  },

  methods: {
    watchNumber(number) {
      let Wnumber = number;
      if (Wnumber < 0) {
        this.$message({
          showClose: true,
          message: "若输入值为负,点击添加会死机呦",
          type: "error"
        });
      }
    },
    add_afterxu() {
      let value = this.after_medical_treatment_scenario_recovery_plan;
      if (value == "") {
        this.$message.error("输入框为空不能添加");
        return false;
      }
      if (!isNaN(value)) {
        this.after_medical_treatment_scenario_recovery_plan_num = parseInt(
          this.after_medical_treatment_scenario_recovery_plan
        );
        return false;
      } else if (isNaN(value)) {
        this.$message({
          message: "请输入数字",
          type: "warning"
        });
        return false;
      }
    },
    add_afterbi() {
      let value = this.after_medical_treatment_scenario_obligatory_exam;
      if (value == "") {
        this.$message.error("输入框为空不能添加");
        return false;
      }
      if (!isNaN(value)) {
        this.after_medical_treatment_scenario_obligatory_exam_num = parseInt(
          this.after_medical_treatment_scenario_obligatory_exam
        );
        return false;
      } else if (isNaN(value)) {
        this.$message({
          message: "请输入数字",
          type: "warning"
        });
        return false;
      }
    },
    add_afterke() {
      let value = this.after_medical_treatment_scenario_optional_exam;
      if (value == "") {
        this.$message.error("输入框为空不能添加");
        return false;
      }
      if (!isNaN(value)) {
        this.after_medical_treatment_scenario_optional_exam_num = parseInt(
          this.after_medical_treatment_scenario_optional_exam
        );
        return false;
      } else if (isNaN(value)) {
        this.$message({
          message: "请输入数字",
          type: "warning"
        });
        return false;
      }
    }
  }
};
</script>
<style scoped>
.txt {
  color: green;
  margin-bottom: 20px;
  display: inline-block;
}
.text {
  color: brown;
  margin-top: 20px;
  display: inline-block;
}
.treatment_days {
  margin-top: 25px;
  margin-left: 66px;
}
.shijian {
  width: 80px;
  height: 30px;
  text-align: center;
  margin-left: 15px;
}
</style>




