<template>
  <div>
    <span class="shu">{{index+1}}</span>
    <input type="text" class="wen" v-model="obligatory_exam_text" name="obligatory_exam_text" />
  </div>
</template>


<script>
export default {
  data() {
    return {
      obligatory_exam_text: ""
    };
  },
  props: ["index"]
};
</script>


<style scoped>
.shu {
  margin: 0 5px;
  color: darkmagenta;
}
.wen {
  width: 300px;
  height: 30px;
  margin-top: 10px;
}
</style>



