<template>
  <div class="treatment_days">
    <span style="color:blue">
      <input type="text" name="treatment_days_tag_name" class="tet" placeholder="场景名字(一般不填)" />
      住院时间：
    </span>
    <br />
    <el-select name="treatment_days" v-model="treatment_days" placeholder="请选择手术时间段单位">
      <el-option
        v-for="item in  time_unit"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      ></el-option>
    </el-select>
    <input
      type="text"
      class="shijian"
      placeholder="最小值"
      name="treatment_min"
      v-model="treatment_min"
      :onchange="watchNumber(treatment_min)"
    />
    <input
      type="text"
      class="shijian"
      placeholder="最大值"
      name="treatment_max"
      v-model="treatment_max"
      @blur="Contrast(treatment_max)"
      :onchange="watchNumber(treatment_max)"
    />
  </div>
</template>
<script>
export default {
  data() {
    return {
      treatment_days: "",
      time_unit: [
        {
          value: "日",
          label: "日"
        },
        {
          value: "时",
          label: "时"
        },
        {
          value: "周",
          label: "周"
        },
        {
          value: "月",
          label: "月"
        }
      ],
      treatment_max: "",
      treatment_min: ""
    };
  },

  props: ["index"],
  methods: {
    watchNumber(number) {
      let Wnumber = number;
      if (Wnumber < 0) {
        this.$message({
          showClose: true,
          message: "不能输入负值呦",
          type: "error"
        });
      }
    },
    Contrast(number) {
      let Min = this.treatment_min;
      if (number < Min) {
        this.$notify.error({
          title: "错误",
          message: "最大值不能小于最小值"
        });
      }
    }
  }
};
</script>
<style scoped>
.treatment_days {
  margin-top: 25px;
}
.shijian {
  width: 100px;
  height: 30px;
  text-align: center;
  margin-left: 70px;
}
.tet {
  display: inline-block;
  width: 115px;
  height: 30px;
}
</style>






